.item {
  font-size: 1.2rem;
  list-style-type: none;
  padding: 17px 0;
  border-bottom: 1px solid #eaeaea;

  & .textInput {
    width: 100%;
    padding: 10px;
    border: 1px solid #dfdfdf;
    background-color: #282c34;
    color: #fff;
  }

  & .checkbox { margin-right: 15px; }

  & .deleteBtn {
    background: transparent;
    border: none;
    float: right;
    cursor: pointer;
    font-size: 1.03rem;
    outline: none;
    height: 50px;
    width: 50px;
    color: #dd443c;
    margin: -10px 0 0 10px;
  }
}
